import { createWebHistory, createRouter } from "vue-router";
import listmmm from './components/list.vue'
import homeKms from './components/homeKms.vue'
import Detail from './components/detail.vue'
import Comment from './components/Comment.vue'
const routes = [
    {
        path: "/List",
        component: listmmm,
    },
    {
        path: "/home",
        component: homeKms,
    },
    {
        // path: "/detail/:id(//d+)", url 주소에 숫자만 입력받아야 하는 경우
        path: "/detail/:id",
        component: Detail,
        children: [
/*             {
                path: 'jejuhotel',
                component: JEJUHOTEL
        }, */
            {
                path: 'comment',
                component: Comment
        },
        ]
    },
    {
        // 아무 문자나 입력할 경우 홈으로 전송하거나 404 페이지 출력
        path: "/:anything(.*)",
        component: homeKms,
/*         beforeEnter: () => {
            if(로그인 == false){
                return '/login'
            }
        }, */
    }
];

const router = createRouter({
    history: createWebHistory(),
    routes, // 라우터 변수로 만들어서 생성하고 사용
});

export default router;